html {
  height: 100%;
}

body {
  background: #ffe;
  height: 100%;
}

#root {
  height: 100%;
}
.expand-button {
  position: absolute;
  top: 3px;
  left: 2px;
  width: 8px;
  height: 12px;
  border-style: groove;
  border-radius: 10px;
  cursor: nw-resize;
}

.expand-help {
  position: absolute;
  top: 22px;
  left: 48px;
  height: fit-content;
  /*width: 52px;*/
  /*cursor: nw-resize;*/
  cursor: pointer;
  text-decoration: underline;
  opacity: 1;
  font-size: 12px;
  font-style: italic;
}

.no-effects {
  font-family: monospace;
  font-size: 14px;
  padding: 4px;
  font-style: italic;
}

.expand-help:hover {
  opacity: 1;
}

.ingredient-div {
  width: fit-content;
  height: fit-content;
  position: relative;
  display: flex;
  align-items: center;
  margin: 4px;
  background: #0007;
  color: #fff;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.ingredient-blur {
  filter: invert(48%) sepia(13%) saturate(3207%) hue-rotate(240deg)
    brightness(35%) contrast(80%);
  opacity: 0.3;
}

.key-selected {
  border: 1px solid #000;
  font-weight: bold;
  filter: contrast(150%);
  /*font-size: 13px;*/
  box-shadow: 0px 0px 0px 3px #0004;
}

.fake-border {
  border: 1px solid;
}

.filling-bkg {
  /* background-color: rgba(255, 0, 0, 0.13); */
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.condiment-bkg {
  /* background-color: rgba(0, 0, 255, 0.14); */
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.active-ingredients-bkg {
  /* background-color: lightgray; */
  padding-top: 4px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.filling-portrait {
  border: 2px solid #d92525;
  border-radius: 10px;
  margin: 0px 2px 0px 2px;
  width: fit-content;
  height: fit-content;
}

.condiment-portrait {
  border: 2px solid #1818be;
  border-radius: 10px;
  margin: 0px 2px 0px 2px;
  width: fit-content;
  height: fit-content;
}

.search-panel {
  margin-top: 10px;
}

.search-bar {
  width: 250px;
  margin: 0px 10px 0px 10px;
}

.search-results-div {
  margin-top: 10px;
}

.complex-row {
  justify-content: center;
}

.settings-bar {
  margin: 10px;
}

.button-spacing {
  cursor: pointer;
  margin: 10px;
}

.ingredient {
  cursor: pointer;
}

.card {
  border: .5px solid #0007;
  text-align: left;
  border-radius: 8px;
  margin: 4px;
  width: fit-content;
  height: fit-content;
  /* min-height: 70px; */
  min-width: 140px;
  padding: 0 4px 4px;
}

.page-break {
  width: 100vw;
}

.pieces {
  display: flex;
  padding-left: 4px;
  margin: 4px;
  margin-top: -10px;
}

.piece-button {
  margin-left: 4px;
  cursor: pointer;
  min-width: 24px;
}

.bubble-row {
  display: flex;
  flex-wrap: wrap;
  min-height: 20px;
  font-size: 16px;
  line-height: 20px;
}

.numbering {
  position: absolute;
  top: 2px;
  left: 4px;
  font-size: 10px;
}

.numbering-icon {
  left: 6px;
  top: 1.5px;
}

.bubble-header {
  font-weight: bold;
  font-size: 14px;
}

.bubble {
  display: flex;
  user-select: none;
  border-radius: 25px;
  padding: 4px;
  width: fit-content;
  height: fit-content;
  font-family: monospace;
  font-size: 14px;
  margin: 2px;
  cursor: pointer;
}

.bubble-min-width {
  min-width: 70px;
  justify-content: center;
}

.bubble-type {
  border-radius: 16px 4px;
}

.chain-a {
  border-bottom-left-radius: 25px;
  border-top-left-radius: 25px;
  border-bottom-right-radius: 0px;
  border-top-right-radius: 0px;
  margin-right: 0px;
  border-right: solid 0.5px #555;
}
.chain-b {
  border-bottom-left-radius: 0px;
  border-top-left-radius: 0px;
  border-bottom-right-radius: 0px;
  border-top-right-radius: 0px;
  margin-right: 0px;
  margin-left: 0px;
  border-right: solid 0.5px #555;
  min-width: 32px;
  justify-content: center;
}
.chain-c {
  border-bottom-left-radius: 0px;
  border-top-left-radius: 0px;
  border-bottom-right-radius: 25px;
  border-top-right-radius: 25px;
  margin-left: 0px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

small {
  font-size: 0.7em;
}

#power-flavors-description {
  position: absolute;
  width: fit-content;
  top: 25px;
  left: 46px;
  font-style: italic;
  font-weight: initial;
  border: 1px dashed;
  border-radius: 25px;
  padding: 4px;
  font-size: 12px;
}

